import { createApp } from "vue";
import App from "../App.vue";

const app = createApp(App);
app.mount("#app");


$(function() {
    $('.app-banner').slide({
        autoPlay: true
    })

    $('.app-tab').slide({
        titCell: ".hd .nav"
    })

    $('.app-slider').slide({
        mainCell: '.wrap',
        prevCell:'.slider-prev',
        nextCell:'.slider-next',
        effect: 'leftLoop',
        vis: 4
    })
})